import React, { useEffect } from "react";
import { createTheme, ThemeProvider } from "@mui/material";
import { dark, light } from "./theme/theme.js";
import { useLoadScript } from '@react-google-maps/api'
import { observer } from "mobx-react-lite";
import Map from './views/Map.js'
import PopupCam from "./components/PopupCam.js";
import Menubar from "./components/Menubar.js";
import Fabs from "./components/Fabs.js"
import { useStore } from "./store/store.js";
import { useMediaQuery, useTheme } from '@mui/material'



const App = () => {

  const { selected, mode, setIsMobile } = useStore()
  
  const theme = useTheme()
  const isMobile = useMediaQuery(theme.breakpoints.down('md'))
  const designTheme = createTheme(mode === 'light'? light : dark)

  const { isLoaded } = useLoadScript({
    googleMapsApiKey: process.env.REACT_APP_MAP_KEY
  })

  useEffect(() => {
    isMobile? setIsMobile(isMobile) : setIsMobile(false)
  }, [isMobile, setIsMobile])

  return(
    <ThemeProvider theme={designTheme}>
      {isLoaded && <Map/>}
      <Menubar>
        {selected && <PopupCam />}
      </Menubar>
      <Fabs/>
    </ThemeProvider>
  )
}

export default observer(App)