import React from "react";
import { observer } from "mobx-react-lite";
import { useStore } from "../store/store";
import { AppBar, styled, IconButton } from "@mui/material";
import { 
  Search, DarkMode, VideoCameraBack, AccountCircle 
} from '@mui/icons-material'


const Menubar = (props) => {

  const {mode, selected, isMobile, setSelected, setMode} = useStore()
  
  const menubar = [{
    icon: <Search/>,
    options: {
      onClick: () => {},
      sx: {}
    }
  },{
    icon: <VideoCameraBack/>,
    options: {
      className: 'videobar',
      onClick: () => setSelected(null),
      sx: {color: selected? 'rgba(30,155,255,0.8)' : ''},
    }
  },{
    icon: <DarkMode/>,
    options: {
      onClick: () => setMode(mode === 'light'? 'dark' : 'light'),
      sx: {color: mode !== 'light'? 'rgba(30,155,255,0.8)' : ''}
    }
  },{
    icon: <AccountCircle/>,
    options: {
      onClick: () => {},
      sx: {}
    }
  }]
  
  return(
    <RootBox 
      mobile={isMobile? 1 : 0}
    >
      {menubar.map((item, index) => 
        <IconButton 
          key={index} 
          {...item.options}
        >
          {item.icon}
          {index===1 && props.children}
        </IconButton>
      )}
    </RootBox>
  )
}

const RootBox = styled(AppBar)(({theme, mobile}) => `
  width: ${mobile? '100vw' : '50px'};
  height: ${mobile? '50px' : '100vh'};
  align-items: center;
  justify-content: space-around;
  flex-direction: ${mobile? 'row' : 'column'};
  padding: 20px 0 20px 0;
  top: unset;
  ${mobile ? 'bottom: 0; left: 0;' : 'top: 0; left: 0;'};
  background: ${theme.palette.menubar.main};
    .videobar{
      position: relative;
    }
`)

export default observer(Menubar)