import React, { useEffect, useRef } from "react";
import { styled } from "@mui/material";
import { useStore } from "../store/store";
import { observer } from "mobx-react-lite";
import { Close } from "@mui/icons-material";



const PopupCam = () => {

  const { selected, setSelected, isMobile } = useStore()
  const ref = useRef()
  
  console.log(111)
  useEffect(() => {
    const timer = setInterval(() => ref.current.src = selected.VideoStreamURL, 3000)

    return () => clearInterval(timer)
  }, [ref, selected])

  return( 
    <RootBox mobile={isMobile? 1 : 0}>
      <div style={{position: 'relative'}}>
        <img src={selected.VideoStreamURL} alt='' ref={ref}/>
        <div className="close" size="small" onClick={() => {
          setSelected(null)
        }}>
          <Close/>
        </div>
      </div>
    </RootBox>
  )
}

export default observer(PopupCam)

const RootBox = styled('div')(({mobile}) => `
  z-index: 2000;
  position: absolute;
  left: ${mobile? '-200%' : '130%'};
  bottom: ${mobile? '130%' : 'unset'};
    img {
      width: ${mobile? '350px' : '450px'};;
      height: ${mobile? '250px' : '350px'};;
      background-size: cover;
    }
    .close {
      width: 30px;
      height: 30px;
      padding: 0;
      position: absolute;
      border-radius: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      top: -4%;
      right: -4%;
      background: rgba(220,220,220,0.6);
      color: black;
    }
`)