import React from 'react'
import { Fab, styled } from '@mui/material'
import { Navigation } from '@mui/icons-material'
import { observer } from 'mobx-react-lite'
import { useStore } from '../store/store'


const Fabs = () => {

  const { setRoadName } = useStore()
  
  return (
    <RootBox>
      <Fab size='small' variant='extended' onClick={() => setRoadName('1')}>
        <Navigation/>
        1
      </Fab>
      <Fab size='small' variant='extended' onClick={() => setRoadName('2')}>
        <Navigation/>
        2
      </Fab>
      <Fab size='small' variant='extended' onClick={() => setRoadName('3')}>
        <Navigation/>
        3
      </Fab>
      <Fab size='small' variant='extended' onClick={() => setRoadName('5')}>
        <Navigation/>
        5
      </Fab>
    </RootBox>
  )
}

export default observer(Fabs)

const RootBox = styled('div')`
  z-index: 2000;
  position: fixed;
  top: 5%;
  right: 3%;
  display: flex;
  flex-direction: column;
  gap: 10px;
    svg {
      margin-right: 10px;
      transform: rotate(30deg);
    }
`