import React, { useEffect, useState } from "react";
import { GoogleMap, Marker, MarkerClusterer } from "@react-google-maps/api";
import { observer } from "mobx-react-lite";
import { useStore } from "../store/store";
import { darkMap } from '../theme/darkMap'
import axios from "axios";
import * as qs from 'qs'


const Map = () => {

  const { token, mode, roadName, setToken, setSelected} = useStore()

  const [center, setCenter] = useState(null)
  const [data, setData] = useState(null)
  const [map, setMap] = useState(null)
  
  const mapOptions = {
    disableDefaultUI: true,
    gestureHandling: "greedy",
    styles: mode === 'light'? '': darkMap,
  }

  useEffect(()=>{
    navigator.geolocation.getCurrentPosition(({ coords })=>{
      setCenter({lat: coords.latitude, lng: coords.longitude})
    })
  },[])

  useEffect(() => {    // take token
    !token && axios({
      method: 'POST',
      url: 'https://tdx.transportdata.tw/auth/realms/TDXConnect/protocol/openid-connect/token',
      headers: {
        'content-type': 'application/x-www-form-urlencoded'
      },
      data: qs.stringify({
        grant_type:"client_credentials",
        client_id: process.env.REACT_APP_TDX_KEY,
        client_secret: process.env.REACT_APP_TDX_VAL
      }),
    }).then(res => setToken(res.data.access_token))

    token && axios({
      url: `https://tdx.transportdata.tw/api/advanced/v2/Road/Traffic/CCTV/Freeway/RoadName/${roadName}?%24top=300&%24format=JSON`,
      headers: {
        "authorization": `Bearer ${token}`
      }
    }).then(res => {
      console.log(res)
      setData(res.data.CCTVs)
    })
  },[token, setToken, roadName])

  useEffect(() => {
    data && map.panTo({lat: data[0].PositionLat, lng: data[0].PositionLon})
  }, [data, map])


  return(
    <GoogleMap 
      mapContainerStyle={{width: '100vw', height: '100vh'}}
      center={center}
      zoom={12}
      options={mapOptions}
      onLoad={map => setMap(map)}
    >
      <MarkerClusterer
        averageCenter
        minimumClusterSize={5}
      >
        {(clusterer) => data?.map((item, i) =>
          <Marker
            key={item.CCTVID}
            position={{lat: item.PositionLat, lng: item.PositionLon}}
            onClick={() => setSelected(item)}
            clusterer={clusterer}
            options={{
              optimized: true, 
              label: {
                text: item.LocationMile.split('+')[0],
                fontSize: '16px',
                color: mode === 'light'? 'black' : 'rgb(220,220,220)'
              },
              icon: {
                path: 'M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z',
                fillOpacity: 1,
                fillColor: mode === 'light'? '#ef5350' : 'orange',
                scale: 1.5,
              }
            }}
          />
        )}
      </MarkerClusterer>
    </GoogleMap>
  )
}

export default observer(Map)